<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
  <el-table
      :data="data.tableData"
      style="width: 100% "
      :header-cell-style="headClass"
      :cell-style="rowClass"
      border
      :default-sort="{prop: 'updateTime', order: 'descending'}"
      :row-class-name="tableRowClassName">
    <el-table-column
        label=""
        type="index"
        width="50"
        align="center">
    </el-table-column>
    <el-table-column prop="id" label="ID" width="50" v-if="false">
    </el-table-column>
    <el-table-column prop="purchaseNo" label="" width="155">
    </el-table-column>
    <el-table-column
        label="" prop="updateTime" width="145">
    </el-table-column>
    <el-table-column
        label="" width="80">
      <template #default="scope">
        <el-popover effect="light" trigger="hover" placement="top">
          <template #default>
            <h3></h3>
            <hr>
            <p>: {{ scope.row.username }}</p>
            <p>: {{scope.row.purchaseNo}}</p>
            <p>: {{ scope.row.consignee }}</p>
            <p>: {{ scope.row.address }}</p>
            <p>: {{ scope.row.cellphone }}</p>
            <p>: {{scope.row.carrier}}{{ scope.row.trackingNo }}</p>
          </template>
          <template #reference>
            <div class="name-wrapper">
              <el-tag size="medium">{{ scope.row.consignee }}</el-tag>
            </div>
          </template>
        </el-popover>
      </template>
    </el-table-column>
    <el-table-column
        label="" prop="cellphone" width="115">
    </el-table-column>
    <el-table-column
        label="" prop="address" width="600">
    </el-table-column>
    <el-table-column
        label="" prop="carrier" width="80">
    </el-table-column>
    <el-table-column
        label="" prop="trackingNo" width="135">
    </el-table-column>
    <el-table-column
        label="" prop="productType" width="160">
    </el-table-column>
    <el-table-column
        label="" prop="orderStatus" width="140">
    </el-table-column>
    <el-table-column
        label="" width="100">
      <template #default="scope">
        <el-progress :text-inside="true" :stroke-width="20" :percentage=scope.row.progress
                     status="exception"></el-progress>
      </template>
    </el-table-column>
    <el-table-column label="">
      <template #default="scope">
        <el-button
            size="mini"
            type="primary" plain
            @click="findCustomer(scope.row)">
        </el-button>
        <el-button
            size="mini"
            type="info" plain
            @click="handleDelete(scope.row)" >
        </el-button>
      </template>
    </el-table-column>
  </el-table>
  </transition>
</template>

<script>

import {ref, onMounted, reactive, getCurrentInstance, computed} from "@vue/composition-api";
import {adminGetNewArrivalOrder} from "@/api/login";
import {deleteById} from "@/api/login";
import gsap from "gsap";

export default {
  name: "NewArrivalOrder",
  setup(props, context) {

    const data = reactive({
          tableData: [{
            id: '',
            purchaseNo: '',
            updateTime: '',
            username: '',
            consignee: '',
            cellphone: '',
            address: '',
            trackingNo: '',
            orderStatus: '',
            productType:'',
            progress: 0,
            carrier:''
          }]
        }
    )

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state ")
      el.style.transform = 'translateX(-60px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition ")
      gsap.to(el, {
        duration: 0.5,
        x: 0,
        opacity: 1,
        onComplete: done,
      })
    })

    const getNewOrder = (() => {
      adminGetNewArrivalOrder().then((response) => {
        console.log("");
        data.tableData = response.data;
        console.log(data.tableData);
      }).catch(() => {
      })
    })

    onMounted(() => {
      getNewOrder()
    })

    const tableRowClassName = (({row, rowIndex}) => {
      if (row.orderStatus === ',') {
        row.progress = 50;

        return 'warning-row-new';
      } else if (row.orderStatus === '') {
        row.progress = 75;
        return 'success-row-new';
      } else if (row.orderStatus === '') {
        row.progress = 100;
        return 'mission-accomplished-row-new';
      } else if (row.orderStatus === '') {
        row.progress = 25;
        return 'changing-or-refunding-row-new';
      }
      return '';
    })

    const {$message} = context.root
    const {$confirm} = context.root
    const {$alert} = context.root
    const {$prompt} = context.root
    const {$router} = context.root


    const findCustomer = ((row) => {
      $router.push('/edit?id=' + row.id)
      console.log(row);
    })


    const handleDelete = ((row) => {
      $confirm('' + row.purchaseNo + '\n' + '[' + row.orderStatus + ']\n' + '.', '',{
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        reconfirm(row);
      }).catch(() => {
        $message.info('')
      })

    })

    const reconfirm = ((row) => {
      $prompt('[]', '', {
        confirmButtonText: '',
        cancelButtonText: '', type: 'warning'
      })
          .then(({value}) => {
            if ('123' === value) {
              deleteById(row.id).then((response) => {
                console.log("")
                if (response.data.code !== 2001 && response.data.code !== 3001) {
                  console.log("")
                  $alert('', '', {
                    confirmButtonText: '',
                    callback: getNewOrder()
                  })
                }else{
                  $alert(',', '', {
                    confirmButtonText: ''
                  })
                }
              })
            } else {
              $message.error(',')
            }
          })
          .catch(() => {
            $message.success('')
          })

    })

    const headClass=(()=>{
      return 'text-align:center;background:#385166;'
    })

    const rowClass=(()=>{
      return 'text-align: center;'
    })

    return {
      data, findCustomer, handleDelete, getNewOrder, tableRowClassName,
      reconfirm,headClass,rowClass,beforeEnter,enter
    }

  }
}
</script>

<style lang="scss">
.el-table .warning-row-new {
  background: #e2c7c7;
  color: #2c2727;
}

.el-table .success-row-new {
  background: #788cb3;
  color: #2c2727;

}

.el-table .mission-accomplished-row-new {
  background: #97a88b;
}

.el-table .changing-or-refunding-row-new {
  background: #cb6c4e;
  color: #2c2727;
}
</style>
