<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div class="wrap">

      <!--    <el-divider><i class="el-icon-shopping-cart-full" style="font-size: 28px; "></i></el-divider>-->
      <div style="margin-top: 20px">
        <div v-show="$route.params.poNumber!=null" class="transition-box">
          <div><i style="color: #59cc7a;font-size: xxx-large" class="el-icon-circle-check"></i></div>
          &nbsp;{{ $route.params.consignee }}&nbsp;{{ items[orderStatus - 1].msg }}
          :&nbsp;{{ $route.params.poNumber }}
          <p>:&nbsp;{{ $route.params.carrier }}&nbsp;{{ $route.params.trackingNo }}</p>
        </div>
        <div>
          <el-collapse-transition>
            <div v-show="show3">
              <div v-show="$route.params.poNumber!=null" style="height: 120px" class="transition-box">
                <el-steps :active="orderStatus" align-center finish-status="success">
                  <el-step :title="item.title" :description="item.description" v-for="item in items"
                           :key="item.id"></el-step>
                </el-steps>

              </div>
              <div style="height: 200px" class="transition-box"><b>:</b>
                <hr>
                {{ $route.params.address }}<br>
                {{ $route.params.consignee }}<br>
                {{ $route.params.cellphone }}<br>
              </div>
            </div>
          </el-collapse-transition>
        </div>
        <el-button type="danger" class="goOnBtn" v-show="!show3" @click="check"></el-button>
      </div>


      <el-button size="medium" autofocus @click="show3 = !show3"></el-button>
      <el-button size="medium" @click="$router.replace('/')"></el-button>


    </div>
  </transition>
</template>

<script>

import gsap from "gsap";

export default {
  name: 'Message',
  data: () => ({
    show3: true,
    orderStatus: 1, //orderStatus
    items: [{title: '', description: '', id: 't1', msg: ','},
      {title: '', description: '', id: 't12', msg: ''},
      {title: '', description: '', id: 't13', msg: ''}
    ],
  }),
  created() {
    if (this.$route.params.orderStatus === ',') {
      this.orderStatus = 1
    }
    if (this.$route.params.orderStatus === '') {
      this.orderStatus = 2
    }    /*trackingNo*/
    if (this.$route.params.orderStatus === '') {
      this.orderStatus = 3
    }
  },
  methods: {
    check() {
      this.$router.replace('/login')
    },

    beforeEnter(el) {
      console.log("beforeEnter-- set initial state ")
      el.style.transform = 'translateY(100px)'
      el.style.opacity = 0
    },

    enter(el, done) {
      console.log(" starting to enter-- make transition ")
      gsap.to(el, {
        duration: 0.5,
        y: 0,
        opacity: 1,
        onComplete: done,
      })
    }


  }
}
</script>

<style>

.wrap {
  text-align: center;
  width: 340px;
  margin: auto;

  /*display: flex;*/
  overflow-x: auto;
  flex: none;
}

.btn {
  margin-top: 10px;
  margin-left: 35%;

}

.goOnBtn {
  margin-top: 10px;
  margin-bottom: 10px;
  /*margin-left: 40.5%;*/
}


.transition-box {
  margin: 0 0 5px 0;
  /*width: 335px;*/
  border-radius: 6px;
  background-color: #324051;
  text-align: center;
  color: #fff;
  padding: 25px 10px;
  box-sizing: border-box;
  /*margin-right: 20px;*/
}
</style>
