import axios from 'axios'
import router from '@/router'
import {showLoading, hideLoading} from "@/utils/loading";


const instance = axios.create({
    baseURL: "http://192.168.50.252:8282",
    timeout: 1000 * 5,
    headers: {'Content-Type': 'application/x-www-form-urlencoded'} //: 'application/x-www-form-urlencoded'??type axios Form Data request.getParameter("username")
});


// 
instance.interceptors.request.use(function (config) {
    // 
    // 
    console.log('')
    let jwt = sessionStorage.getItem('jwt')
    if (jwt) {
        config.headers['authorization'] = jwt;
    } // tokensessionStorageAuthorization
    console.log('')
    console.log(config)
    console.log(config.headers)
    console.log(config.headers.authorization)
    // 
    showLoading();
    return config;
}, function (error) {
    return Promise.reject(error);
});

// 
instance.interceptors.response.use(function (response) {
    // 
    let jwt =response.headers['authorization'];
    if(jwt){
        sessionStorage.setItem('jwt', jwt);
    }
    console.log('')
    console.log(sessionStorage['jwt'])

    hideLoading();
    return response;
}, function (error) {
    // 
    hideLoading();
    return Promise.reject(error);
});

export default instance


