<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div class="edit-wrap">
      <el-collapse-transition>
        <div v-show="show3">
          <el-form style="width: 600px" ref="form" :model="form" class="edit-box" size="small">
            <el-divider><span class="iconfont iconsonghuo" style="font-size: 25px; "></span></el-divider>
            <h3>:</h3>

            <el-form-item class="form-item" v-show="false">
              <label>ID</label>
              <el-input v-model="form.id" readonly></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label></label>
              <el-input v-model="form.purchaseNo" readonly  style="width: 180px"
                        prefix-icon="el-icon-success"></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label></label>
              <el-input v-model="form.consignee" readonly  style="width: 155px"
                        prefix-icon="el-icon-user-solid"></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label></label>
              <el-input v-model="form.cellphone" readonly  style="width: 155px" prefix-icon="el-icon-phone"></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label></label>
              <el-input v-model="form.address" readonly  prefix-icon="el-icon-location"></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label></label>
              <el-input v-model="form.productType" readonly  style="width: 200px"
                        prefix-icon="el-icon-s-goods"></el-input>
            </el-form-item>
            <el-alert style="margin: 25px 0 15px 0"
                title=",,"
                type="info"
                :closable="false"
                effect="dark">
            </el-alert>
            <el-form-item class="form-item">
              <label><span class="iconfont iconcaiwu2" style="color: #db8888;font-size: 20px"></span></label>
              <el-input v-model="form.trackingNo" autocomplete="off" placeholder="" style="width: 400px">
                <el-select v-model="form.carrier" slot="prepend" placeholder="" style="width: 150px">
                  <el-option label="EMS" value="EMS"></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value="others"></el-option>
                </el-select>
              </el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label><span class="iconfont iconcaiwu2" style="color: #7eccda;font-size: 20px"></span></label>
              <el-select v-model="form.orderStatus" placeholder="" style="width: 180px">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="," value=","></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
              <el-button type="primary" style="margin-left: 50px" @click="onSubmit('form')"></el-button>
              <el-button @click="cancel"></el-button>
            </el-form-item>
            <!--          <el-form-item class="form-item">
                        <label></label>
                        <el-input v-model="form.trackingNo" autocomplete="off" placeholder=""></el-input>
                      </el-form-item>-->
            <!--          <el-form-item class="form-item">
                        <el-button type="primary" @click="onSubmit('form')"></el-button>
                        <el-button @click="cancel"></el-button>
                      </el-form-item>-->
          </el-form>
        </div>
      </el-collapse-transition>
    </div>
  </transition>
</template>


<script>

import {findById, updateById} from "@/api/login"
import gsap from "gsap";

export default {
  name: "Edit",
  data() {
    return {
      form: {
        id: '',
        purchaseNo: '',
        consignee: '',
        cellphone: '',
        address: '',
        productType: '',
        carrier: '',
        trackingNo: '',
        orderStatus: '',
      },
      show3: true,
    }
  },
  created() {
    let id = this.$route.query.id
    let that = this
    console.log(id)
    findById(id).then((response) => {
      that.form = response.data
      console.log(response.data)
    })
  },

  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let these = this
          let customerInfoWithTrackingInfoAndOrderStatusChanged = this.form
          updateById(customerInfoWithTrackingInfoAndOrderStatusChanged).then((response) => {
            console.log(response.data);
            if (response.data) {
              this.show3 = false;
              setTimeout(() => {
                these.$alert('[ ' + these.form.purchaseNo + ' ],\n: "' + these.form.orderStatus + '"\n' + ': ' + these.form.carrier + ' ' + these.form.trackingNo, '', {
                  confirmButtonText: '', callback: () => {
                    these.$router.push('/admin/order')
                    this.open1()
                  }
                })
              }, 500)
            }
          })
        }
      })
    },

    cancel() {
      this.show3 = false;
      setTimeout(() => {
        this.$router.push('/admin/order')
      }, 500);
      this.open2()
    },

    open1() {
      this.$notify({
        title: '',
        message: '',
        type: 'success'
      });
    },

    open2() {
      this.$notify({
        title: '',
        message: '',
        type: 'info'
      });
    },

    beforeEnter(el) {
      console.log("beforeEnter-- set initial state ")
      el.style.transform = 'translateX(100px)'
      el.style.opacity = 0
    },

    enter(el, done) {
      console.log(" starting to enter-- make transition ")
      gsap.to(el, {
        duration: 1,
        x: 0,
        opacity: 1,
        onComplete: done,
      })
    }
  }
}
</script>

<style lang="scss" scoped>

.edit-wrap {
  width: 600px;
  margin: auto;
}

.edit-box {
  margin-top: 29px;

  label {
    display: block;
    margin-bottom: 3px;
    font-size: 14px;
    color: azure;
  }

  .form-item {
    margin-bottom: 13px;
  }

}

.el-notification {
  white-space: pre-wrap !important;
}

.el-message-box__content {
  white-space: pre-wrap !important;
}


</style>
