<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <el-table
        :data="data.tableData"
        style="width: 100% "
        :header-cell-style="headClass"
        border
        :default-sort="{prop: 'updateTime', order: 'descending'}"
        :row-class-name="tableRowClassName">
      <el-table-column
          label=""
          type="index"
          width="50"
          align="center">
      </el-table-column>
      <el-table-column prop="id" label="ID" width="50" v-if="false">
      </el-table-column>
      <el-table-column prop="purchaseNo" label="" width="160">
      </el-table-column>
      <el-table-column
          label="" prop="updateTime" width="150">
      </el-table-column>
      <el-table-column
          label="" prop="consignee" width="130">
      </el-table-column>
      <el-table-column
          label="" prop="cellphone" width="115">
      </el-table-column>
      <el-table-column
          label="" prop="address" width="800">
      </el-table-column>
      <el-table-column
          label="" prop="carrier" width="80" v-if="false">
      </el-table-column>
      <el-table-column
          label="" prop="trackingNo" width="135" v-if="false">
      </el-table-column>
      <el-table-column
          label="" prop="productType" width="160" v-if="false">
      </el-table-column>
      <el-table-column
          label="" prop="orderStatus" width="140" v-if="false">
      </el-table-column>
      <el-table-column
          label="" width="100" v-if="false">
        <template #default="scope">
          <el-progress :text-inside="true" :stroke-width="20" :percentage=scope.row.progress
                       :color=scope.row.progressColor></el-progress>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template #default="scope">
          <el-button
              size="mini"
              type="danger" plain
              @click="findCustomer(scope.row)">
          </el-button>
          <el-button v-if="false"
                     size="mini"
                     type="info" plain
                     @click="handleDelete(scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </transition>
</template>

<script>

import {onMounted, reactive} from "@vue/composition-api";
import {adminGetOrder} from "@/api/login";
import {deleteById} from "@/api/login";
import gsap from "gsap";

export default {
  name: "Order",
  setup(props, context) {

    const data = reactive({
          tableData: [{
            id: '',
            purchaseNo: '',
            updateTime: '',
            username: '',
            consignee: '',
            cellphone: '',
            address: '',
            trackingNo: '',
            orderStatus: '',
            progress: 0,
            carrier: '',
            productType: '',
            progressColor: ''
          }]
        }
    )

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state ")
      el.style.transform = 'translateX(100px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition ")
      gsap.to(el, {
        duration: 2,
        x: 0,
        opacity: 1,
        onComplete: done,
      })
    })

    const getOrder = (() => {
      adminGetOrder().then((response) => {
        console.log("");
        data.tableData = response.data;
        console.log(data.tableData);
      }).catch(() => {
      })
    })

    onMounted(() => {
      getOrder();

    })

    const tableRowClassName = (({row, rowIndex}) => {
      if (row.orderStatus === ',') {
        row.progress = 50;
        row.progressColor = '#c05656'
        return 'warning-row';
      } else if (row.orderStatus === '') {
        row.progress = 75;
        row.progressColor = '#6893b0';
        return 'success-row';
      } else if (row.orderStatus === '') {
        row.progress = 100;
        row.progressColor = '#81967b'
        return 'mission-accomplished-row';
      } else if (row.orderStatus === '') {
        row.progress = 35;
        row.progressColor = '#da3b06';
        return 'changing-or-refunding-row';
      }
      return '';
    })





    const {$message} = context.root
    const {$confirm} = context.root
    const {$alert} = context.root
    const {$prompt} = context.root
    const {$router} = context.root


    const findCustomer = ((row) => {
      $router.push('/editAll?id=' + row.id)
      console.log(row);
    })


    const handleDelete = ((row) => {
      $confirm('' + '[\n' + row.purchaseNo + '\n]' + '[\n' + row.orderStatus + '\n]' + '', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        reconfirm(row);
      }).catch(() => {
        $message.info('')
      })

    })

    const reconfirm = ((row) => {
      $prompt('[]', '', {
        confirmButtonText: '',
        cancelButtonText: '', type: 'warning'
      })
          .then(({value}) => {
            if (value === '123') {
              deleteById(row.id).then((response) => {
                if (response.data) {
                  $alert('', '', {
                    confirmButtonText: '',
                    callback: getOrder()
                  })
                }
              })
            } else {
              $message.error(',')
            }
          })
          .catch(() => {
            $message.success('')
          })

    })

    const headClass = (() => {
      return 'text-align:center;background:#313C50;'
    })


    return {
      data, findCustomer, handleDelete, getOrder, tableRowClassName,
      reconfirm, headClass,beforeEnter,enter
    }

  }
}

</script>

<style lang="scss">


.el-table .warning-row {
  background: #dfb0b0;
  color: #ba4747;

}


.el-table .success-row {
  background: #8fa6b7;
  color: #4d4545;


}


.el-table .mission-accomplished-row {
  background: #8fa588;
  color: #4d4545;

}


.el-table .changing-or-refunding-row {
  background: #bb725a;
  color: #1e1e1e;
}

</style>


