<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div>
      <div style="text-align: right;">

        <el-input type="text" v-model="searchValue" style="width: 200px" @keyup.enter.native="searchAction"
                  placeholder=""/>

        <el-button size="mini" style="border-radius: 0" icon="el-icon-search" @click="searchAction"></el-button>
        <el-button size="mini" style="border-radius: 0" class="iconfont iconpangxie2" @click="getOrder"> 
        </el-button>

        <el-button size="mini" style="border-radius: 0" class="iconfont iconpangxie2" @click="fileOutputConfirm"> 
        </el-button>
      </div>
      <el-table id="table"
                :data="data.tableData"
                style="width: 100%"
                :header-cell-style="headClass"
                :cell-style="rowClass"
                border
                :default-sort="{prop: 'updateTime', order: 'descending'}"
                :row-class-name="tableRowClassName">
        <el-table-column
            label=""
            type="index"
            width="50"
            align="center">
        </el-table-column>
        <el-table-column prop="id" label="ID" width="55" v-if="false">
        </el-table-column>
        <el-table-column prop="purchaseNo" label="" width="155">
        </el-table-column>
        <el-table-column
            label="" prop="updateTime" width="145">
        </el-table-column>
        <el-table-column
            label="" width="80">
          <template #default="scope">
            <el-popover effect="light" trigger="hover" placement="left">
              <template #default>
                <h3></h3>
                <hr>
                <p>: {{ scope.row.username }}</p>
                <p>: {{ scope.row.purchaseNo }}</p>
                <p>: {{ scope.row.consignee }}</p>
                <p>: {{ scope.row.address }}</p>
                <p>: {{ scope.row.cellphone }}</p>
                <p>: {{ scope.row.carrier }}{{ scope.row.trackingNo }}</p>
              </template>
              <template #reference>
                <div class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.consignee }}</el-tag>
                </div>
              </template>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column
            label="" prop="cellphone" width="115">
        </el-table-column>
        <el-table-column
            label="" prop="address" width="580">
        </el-table-column>
        <el-table-column
            label="" prop="carrier" width="80">
        </el-table-column>
        <el-table-column
            label="" prop="trackingNo" width="140">
        </el-table-column>
        <el-table-column
            label="" prop="productType" width="185">
        </el-table-column>
        <el-table-column
            label="" prop="orderStatus" width="140">
        </el-table-column>
        <el-table-column
            label="" width="90">
          <template #default="scope">
            <el-progress :text-inside="true" :stroke-width="20" :percentage=scope.row.progress
                         :color=scope.row.progressColor></el-progress>
          </template>
        </el-table-column>
        <el-table-column label="" align="right">


          <template #default="scope">
            <el-button
                size="mini"
                type="primary" plain
                @click="findCustomer(scope.row)">
            </el-button>
            <el-button
                size="mini"
                type="info" plain
                @click="handleDelete(scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
          background
          layout="prev, pager, next"
          :page-size="pgSize"
          :currentPage="1"
          :total="total"
          @current-change="page">
      </el-pagination>
    </div>
  </transition>
</template>

<script>

import {onMounted, reactive, ref} from "@vue/composition-api";
import {adminGetOrder, adminGetOrderSearch, adminGetOrderNextPage} from "@/api/login";
import {deleteById} from "@/api/login";
import gsap from "gsap";
import XLSX from "xlsx";
import {Loading} from "element-ui";

export default {
  name: "Order",
  setup(props, context) {

    const openFullScreen2FileOutput = (() => {
      let loading = Loading.service({
        lock: true,
        text: '...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
      }, 5000);
    })

    const fileOutputConfirm = (() => {
      $confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        exportToExcel();
        openFullScreen2FileOutput();
        setTimeout(() => {
          $message({
            type: 'success',
            message: '!'
          })
        }, 5200)
      }).catch(() => {
        $message({
          type: 'info',
          message: ''
        });
      });
    })


    const exportToExcel = (() => {
      let table = document.getElementById('table');
      let workBook = XLSX.utils.table_to_book(table);
      try {
        XLSX.writeFile(workBook, 'yourFile.xlsx');
      } catch (e) {
        console.log(e);
      }
    })

    const searchValue = ref('')

    const searchAction = (() => {
      let requestData = {
        keyWords: searchValue.value
      }
      if (searchValue.value.trim() === '') {
        openMsg();
        return;
      }
      adminGetOrderSearch(requestData).then((response) => {
        data.tableData = response.data;
        total.value = response.data.length
        pgSize.value = response.data.length
        //console.log(response.data.length)
        console.log(response)
        //keyWordsShowColor(requestData.keyWords,response.data)
      }).catch(error => {
      })
    })

    const openMsg = (() => {
      $alert('<strong>...</strong>', '', {
        confirmButtonText: '',
        dangerouslyUseHTMLString: true,
        type: 'warning',
        showClose: false,
        callback: action => {
          page(1)
        }

      });
    })

    /*    const keyWordsShowColor = ((key,array) => {
        })*/


    const data = reactive({
          tableData: [{
            id: '',
            purchaseNo: '',
            updateTime: '',
            username: '',
            consignee: '',
            cellphone: '',
            address: '',
            trackingNo: '',
            orderStatus: '',
            progress: 0,
            carrier: '',
            productType: '',
            progressColor: ''
          }],
        }
    )

    const total = ref(0)
    const pgSize = ref(0)

    const page = ((currentPage) => {
      let requestData = {
        currentPage: currentPage,
        pageSize: 30
      }
      adminGetOrderNextPage(requestData).then((response) => {
        console.log(response)
        data.tableData = response.data.records;
        total.value = response.data.total;
        pgSize.value = requestData.pageSize;
      }).catch((err) => {
      })
    })

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state ")
      el.style.transform = 'translateY(-60px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition ")
      gsap.to(el, {
        duration: 0.7,
        y: 0,
        opacity: 1,
        onComplete: done,
      })
    })

    const getOrder = (() => {
      adminGetOrder().then((response) => {
        console.log("");
        data.tableData = response.data;
        total.value = response.data.length
        pgSize.value = response.data.length
        console.log(data.tableData);
        console.log(response)
      }).catch(() => {
      })
    })

    onMounted(() => {
      page(1);

    })

    const tableRowClassName = (({row, rowIndex}) => {
      if (row.orderStatus === ',') {
        row.progress = 50;
        row.progressColor = '#c05656'
        return 'warning-row';
      } else if (row.orderStatus === '') {
        row.progress = 75;
        row.progressColor = '#7188b0';
        return 'success-row';
      } else if (row.orderStatus === '') {
        row.progress = 100;
        row.progressColor = '#81967b'
        return 'mission-accomplished-row';
      } else if (row.orderStatus === '') {
        row.progress = 35;
        row.progressColor = '#da3b06';
        return 'changing-or-refunding-row';
      }
      return '';
    })

    const headClass = (() => {
      return 'text-align:center;background:#385166;'
    })

    const rowClass = (() => {
      return 'padding:1px'

    })


    /*    const hint = (({row, rowIndex}) => {
          switch (row.orderStatus) {
            case ',': {
              data.tableData.progress =50
              break;
            }
            case '': {
              data.tableData.progress = 75
              break;
            }
            case '': {
              data.tableData.progress = 100
              break;
            }
            case '': {
              data.tableData.progress = 25
            }
          }
        })*/


    const {$message} = context.root
    const {$confirm} = context.root
    const {$alert} = context.root
    const {$prompt} = context.root
    const {$router} = context.root


    const findCustomer = ((row) => {
      $router.push('/edit?id=' + row.id)
      console.log(row);
    })


    const handleDelete = ((row) => {
      $confirm('' + row.purchaseNo + '\n' + '[' + row.orderStatus + ']\n' + '.', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        reconfirm(row);
      }).catch(() => {
        $message.info('')
      })

    })

    const reconfirm = ((row) => {
      $prompt('[]', '', {
        confirmButtonText: '',
        cancelButtonText: '', type: 'warning'
      })
          .then(({value}) => {
            if ('123' === value) {
              deleteById(row.id).then((response) => {
                console.log("")
                if (response.data.code !== 2001 && response.data.code !== 3001) {
                  console.log("")
                  $alert('', '', {
                    confirmButtonText: '',
                    callback: page(1)
                  })
                }else{
                  $alert(',', '', {
                    confirmButtonText: ''
                  })
                }
              })
            } else {
              $message.error(',')
            }
          })
          .catch(() => {
            $message.success('')
          })

    })


    return {
      data,
      findCustomer,
      handleDelete,
      getOrder,
      tableRowClassName,
      reconfirm,
      headClass,
      rowClass,
      beforeEnter,
      enter,
      page,
      total,
      searchAction,
      searchValue,
      pgSize,
      openMsg,
      exportToExcel,
      fileOutputConfirm,
      openFullScreen2FileOutput
      //keyWordsShowColor
    }

  }
}

</script>

<style lang="scss">

div > .el-message-box {
  vertical-align: 22vh;

}

div > .el-input > input {
  height: 27px;
  border-radius: 0;
}


.el-table .warning-row {
  background: #dfbcbc;
  color: #ba4747;

}


.el-table .success-row {
  background: #7188b0;
  color: #4d4545;


}


.el-table .mission-accomplished-row {
  background: #859d7e;
  color: #4d4545;

}


.el-table .changing-or-refunding-row {
  background: #bb725a;
  color: #1e1e1e;
}

</style>


